"use client";
import MultiSelect from '@/app/components/multiSelect';
const Select = dynamic(() => import('react-select'), {
    ssr: false,
});
import dynamic from 'next/dynamic';
import '../../../../../../public/sass/pages/multiSelect.scss';
import React, { useEffect, useState } from 'react';
import { Button, Card, Col, Form, InputGroup, Row } from 'react-bootstrap';
import '../../../../../../public/sass/pages/add.scss';
import '../../../../../../public/sass/pages/homePage.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEye, faEyeSlash, faRedo, faTimes, } from '@fortawesome/free-solid-svg-icons';
import NavBottom from '@/app/components/navBottom';
import axios from 'axios';
import { toast, ToastContainer } from 'react-toastify';
import { useParams, useRouter } from 'next/navigation';
import { toBase64 } from '../../../../helpers'



const EditBLog = () => {

    const router = useRouter()

    const params = useParams();

    const [category, setCategory] = useState([])

    const [newData, setNewData] = useState({});

    const [blogData, setBlogData] = useState({});

    const [imgData, setImgData] = useState({});

    const [img, setImg] = useState("");

    const fetchCategories = async () => {
        try {
            let resp = await axios.get("http://localhost:4000/admin/blogsCategory")
            console.log("###", resp)
            if (resp.status === 200) {
                setCategory(resp.data.message.map(cat => ({
                    value: cat._id,
                    label: cat.blogCategoryTitle
                })));
            }
        } catch (error) {
            toast.error("Failed to load categories")
        }
    }

    const defaultData = async () => {
        try {
            if (!params.id) return;
            let resp = await axios.get(`http://localhost:4000/admin/blog/view/${params.id}`)
            console.log("default value", resp);
            if (resp.status === 200) {
                console.log("dascadsc", resp.data.data.category)
                resp.data.data.category = resp.data.data.category.map(item => { return { value: item, label: category.find(cat => cat.value == item)?.label } });
                console.log("222", resp.data.data)
                setBlogData(resp.data.data)
                console.log("++", resp.data)
            }
        } catch (error) {
            toast.error("Not able to fetch current input of blog")
        }
    }


    const updatedBlogSubmit = async (e) => {
        e.preventDefault();

        try {

            let finalUpdatedData = {
                ...newData,
                imageUrl: { ...imgData },
                selected_img: img
            }

            let resp = await axios.put(`http://localhost:4000/admin/blog/edit/${params.id}`, finalUpdatedData)
            console.log("///", resp)

            if (resp.status !== 200) {
                toast("NOt able to update Blog")
                return;
            }

            else if (resp.status === 200) {
                setNewData({
                    title: "",
                    publishedOn: "",
                    subHeading: "",
                    description: "",
                    category: ""
                })
                setTimeout(() => { router.push('/blogs') }, 2000)
                toast(" Blog Updated Successfully")
            }
            else {
                toast("CAtegory not upadted")
            }
        } catch (error) {
            return ("Error in updated Blog", error)
        }
    }

    const updatedBlogChange = (e) => {
        if (!e) return;

        if (Array.isArray(e)) {
            setNewData((prevdata) => ({
                ...prevdata,
                category: e.map(item => item.value)
            }));
        }
        else {
            const { name, value } = e.target;
            setNewData((prevData) => ({
                ...prevData,
                [name]: value
            }));
        }
    }


    const onFileChange = (e) => {
        uploadClick(e);
        e.currentTarget.value = "";
    }

    const uploadClick = async (e) => {
        const file = e.target.files?.[0];
        if (file) {
            const base64 = await toBase64(file);
            console.log("base64", base64);
            let saveImage = {
                folder_name: "admins",
                resize_large: "1266*668",
                resize_medium: "1166*568",
                resize_small: "1066*468",
                image: base64 ? base64 : null
            }
            let resp = await axios.post("http://localhost:4000/uploads/base64", saveImage,
                {
                    onUploadProgress: (e) => {
                        const percentCompleted = Math.round((e.loaded * 100) / e.total);
                        requestAnimationFrame(() => {
                            setProgress(percentCompleted)
                        })
                    }
                });
            console.log('resp', resp);
            if (resp.data.imageUrl) {
                setImgData(resp.data.imageUrl);
                setProgress(0)
                setImg(resp.data.imageUrl.original)
            }
        }
    }

    const handleDelete = () => {
        blogData.selected_img = '';
    }

    useEffect(() => {
        fetchCategories();
    }, [])

    useEffect(() => {
        defaultData();
    }, [category])
    // console.log(blogData.category)

    const [show, setShow] = useState(false);
    const [showPass, setShowPass] = useState(false);

    return (
        <div className='right_side'>
            <NavBottom backUrl="/blogs" />
            <div className="right_area top_spacing">
                <Card>
                    <div className='card-header'>
                        <div className='header_left'>
                            <div className='heading'>
                                Edit Your Blog Here
                            </div>
                        </div>
                    </div>
                    <div className='card-body'>
                        <Form onSubmit={updatedBlogSubmit}>
                            <Row>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Title <span>*</span></Form.Label>
                                        <Form.Control
                                            name='title'
                                            defaultValue={blogData.title}
                                            type="text"
                                            placeholder="Enter Blog Title"
                                            onChange={updatedBlogChange}
                                            required
                                        />
                                    </Form.Group>
                                </Col>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Published On <span>*</span></Form.Label>
                                        <Form.Control
                                            name='publishedOn'
                                            defaultValue={blogData.publishedOn}
                                            type="date"
                                            placeholder="Enter Current Date"
                                            onChange={updatedBlogChange}
                                            required
                                        />
                                    </Form.Group>
                                </Col>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Creator Name<span>*</span></Form.Label>
                                        <Form.Control
                                            name='creatorName'
                                            defaultValue={blogData.creatorName}
                                            type="text"
                                            placeholder="Enter Creator Name"
                                            onChange={updatedBlogChange}
                                            required
                                        />
                                    </Form.Group>
                                </Col>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Category<span>*</span></Form.Label>
                                        <Select
                                            className="select_main"
                                            classNamePrefix="select"
                                            placeholder={"Select the Category"}
                                            isClearable={false}
                                            isMulti={true}
                                            name="category"
                                            defaultValue={blogData.category}
                                            onChange={updatedBlogChange}
                                            options={category}
                                            required
                                        />
                                    </Form.Group>
                                </Col>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Sub Heading<span>*</span></Form.Label>
                                        <Form.Control
                                            name='subHeading'
                                            defaultValue={blogData.subHeading}
                                            type="text"
                                            placeholder="Enter Sub Heading"
                                            onChange={updatedBlogChange}
                                            required
                                        />
                                    </Form.Group>
                                </Col>
                                <Col xxl={12} xl={12} lg={12} md={12} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Derscription <span>*</span></Form.Label>
                                        <Form.Control
                                            name='description'
                                            defaultValue={blogData.description}
                                            as="textarea"
                                            placeholder="Enter Description"
                                            onChange={updatedBlogChange}
                                            required
                                        />
                                    </Form.Group>
                                </Col>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Image <span>*</span></Form.Label>
                                        <div className='d-felx align-item-center gap-3 mb-2'>
                                            <Form.Label htmlFor="file-upload" className='upload'>Upload Image</Form.Label>
                                            <Form.Control
                                                type="file"
                                                id="file-upload"
                                                placeholder="Upload Image"
                                                onChange={onFileChange}
                                                onClick={uploadClick}
                                            />
                                            {
                                                Object.keys(imgData).length !== 0 &&
                                                <Form.Select aria-label="Default select example" onChange={(e) => setImg(e.target.value)}>
                                                    {
                                                        Object.entries(imgData).map(([key, value], i) => {
                                                            return <option value={value} key={i}>{key}</option>
                                                        })
                                                    }
                                                </Form.Select>
                                            }
                                        </div>
                                        {/* after upload --------- */}

                                        <div className='upload_img_area'>

                                            <div className='img_area'>
                                                <span className='cross_icon' onClick={handleDelete}>
                                                    <FontAwesomeIcon icon={faTimes} />
                                                </span>
                                                <img
                                                    src={process.env.imageUrl + '' + blogData.selected_img}
                                                    alt='blog Image'
                                                    priority="low"
                                                />
                                            </div>

                                        </div>
                                    </Form.Group>
                                </Col>
                                <Col xxl={6} xl={6} lg={6} md={6} sm={12} xs={12}>
                                    <Form.Group className='form-group'>
                                        <Form.Label>Send credentials on email ?</Form.Label>
                                        <Form.Check
                                            type="switch"
                                            id="custom-switch"
                                            label="Send credentials on email ?"
                                            checked={show}
                                            onChange={(event) => setShow(event.target.checked)}
                                        />
                                    </Form.Group>

                                    {show && <Form.Group className='form-group form-group2'>
                                        <InputGroup>
                                            <Form.Control
                                                placeholder="credentials"
                                                type={showPass ? "text" : "password"}
                                                className='from-control2'
                                            />
                                            <InputGroup.Text id="inputGroupPrepend" onClick={() => setShowPass(!showPass)}>
                                                <FontAwesomeIcon icon={showPass ? faEye : faEyeSlash} />
                                            </InputGroup.Text>
                                            <InputGroup.Text id="inputGroupPrepend"><FontAwesomeIcon icon={faRedo} /></InputGroup.Text>
                                        </InputGroup>
                                    </Form.Group>}
                                </Col>
                                <div className='btn_area'>
                                    <Button type="submit" >Submit</Button>
                                </div>
                            </Row>
                        </Form>
                    </div>
                </Card>
            </div>
            <ToastContainer
                closeButton={true}
                closeOnClick={true}
                newestOnTop={true}
                stacked={true}
                limit={5}
                autoClose={2000}
                toastStyle={{ backgroundColor: '#696cff', color: 'white' }}
                position='bottom-right'
                theme='colored'
            />
        </div>
    )
}

export default EditBLog;